import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import {BrowserRouter, Route, Routes, HashRouter} from "react-router-dom";
import SectionWrapper from "./components/SectionWrapper";

import Test1 from "./routes/Test1";
import PleaseRegister from "./routes/PleaseRegister";
import BasicData from "./routes/BasicData";
import Startform from "./routes/Startform";

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
        <BrowserRouter basename="/crewtime">
            <Routes>
                <Route path="/" element={<App/>}>
                    <Route index element={<PleaseRegister/>}/>
                    <Route path="/pleaseregister" element={<PleaseRegister/>}/>
                    <Route path="/basicdata" element={<BasicData/>}/>
                    <Route path="/startform" element={<Startform/>}/>
                    <Route path="/test1" element={<Test1/>}/>
                    <Route path="/*" element={
                        <SectionWrapper>
                            <iframe src="https://giphy.com/embed/KBYyUGgDEsILK" width="480" height="240" frameBorder="0"
                                    className="giphy-embed" allowFullScreen></iframe>
                            <h1><strong>404</strong></h1>
                        </SectionWrapper>
                    }/>
                </Route>
            </Routes>
        </BrowserRouter>
);


